.card__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0;
  padding-top: 0.5rem;
  margin: 0.3rem;
  border: 1px solid #efefef;
  border-radius: 0.2rem;
  min-width: 160px;
  text-align: center;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.089);
  cursor: pointer;
  min-height: 100px;
  border-radius: 20% 20% 20% 0%;

}

.card__container:hover {
  transform: scale(1.4) ;
  transition: .4s ease;
}

.card__name {
  text-transform: uppercase;
}
.card__container .number {
  border-radius: 1rem;
  padding: 0.2rem 0.4rem;
  background-color: rgba(167, 167, 167, 0.562);
  position: relative;
  left: 45%;
}
.card__container img {
  position: relative;
  width: 140px;
  height: 140px;
  left: 30%;
  margin: 0;
}

.card__detail {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.card__detail button {
  color: rgb(22, 22, 22);
  padding: 0.5rem;
  margin-top: 1rem;
  border: none;
  border-radius: 0.2rem;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.185);
}
.load-more {
  background: #f5f5f5 linear-gradient(#f5f5f5, #f1f1f1) repeat-x;
  border-radius: 2px;
  border: 1px solid #c6c6c6;
  color: #444;
  padding: 0.5rem 1.5rem;
  min-width: 50%;
  margin-top: 1rem;
}

